body {
  background-color: lightgray;
  margin: 20px;
}

.app-container {
  grid-template-columns: 1fr;
}

@media (min-width: 600px) {
  .app-container {
    display: grid;
    grid-template-columns: 200px 1fr;
    gap: 20px;
  }
}

.notes-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  grid-auto-rows: minmax(250px, auto);
  gap: 20px;
}

.note-item {
  word-break: break-word;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  border: 1px solid #ccc;
  padding: 10px;
  border-radius: 5px;
  background-color: #f9f9f9;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  cursor: pointer;

  .notes-header {
    display: flex;
    justify-content: flex-end;

    button {
      font-size: 16px;
      background: transparent;
      border: none;
      cursor: pointer;
      max-width: fit-content;
    }
  }
}

h2 {
  margin: 0;
}
.disclaimer-title, .disclaimer-p {
  margin: 0;
}
.note-form {
  display: flex;
  flex-direction: column;
  gap: 20px;

  button {
    border-radius: 5px;
    background-color: rgb(64, 154, 184);
    border: none;
    padding: 10px;
    font-size: 16px;
    color: white;

    &:hover {
      background-color: rgb(106, 175, 198);
      cursor: pointer;
    }
  }
}

textarea,
input {
  border-radius: 5px;
  border: 1px solid black;
  padding: 10px;
  font-size: 16px;
}

.edit-buttons {
  display: flex;
  justify-content: space-evenly;
  gap: 5px;

  button {
    flex: 1;

    &:last-of-type {
      background-color: rgb(220, 89, 89);
      color: white;
    }
  }
}